.home{
    position: relative;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &__text-wrapper{
        display: flex;
        justify-content: center;
        align-items: center;

        h1{
            color: var(--selected-theme-main-colour);
            margin: 0px;
            font-weight: 400;
            font-size: 8rem;
            line-height: 10rem;
            position: relative;
            // TODO: Better Font management in themes
            font-family: "Inconsolata",Consolas,monospace;
            overflow: hidden;
            white-space: nowrap;
            animation: typingEffect 2.5s steps(30, end)// delay iteration-count direction fill-mode;
        }

    }
    .contact-me{
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;
        margin-top: 10px;
        &__buttons-wrapper{
            display: flex;
            gap: 10px;
            button, a {
                cursor: pointer;
                padding: 10px 70px;
                font-size: 2rem;
                background: transparent;
                color: var(--selected-theme-main-colour);
                border: 1.5px solid var(--selected-theme-main-colour);
                border-radius: 4px;
                font-weight: 500px;
                //TODO: Hover seems broken
                &:hover {
                    color: #000000;
                    background: var(--selected-theme-main-colour);
                }
            }
        }
        &__socials-wrapper{
            display: flex;
            gap: 10px;
            
            svg{
                color: var(--selected-theme-main-colour);
                cursor: pointer;
            }
        }

    }

}

@media only screen and (max-width:550px) {
    .home__text-wrapper h1{
        font-size: 3rem;
        line-height: 5rem;
    }
}

@media only screen and (min-width : 551px) and (max-width:768px) {
    .home__text-wrapper h1{
        font-size: 4rem;
        line-height: 6rem;
    }
}
@media only screen and (min-width : 769px) and (max-width:990px) {
    .home__text-wrapper h1{
        font-size: 6rem;
        line-height: 8rem;
    }
}


@keyframes typingEffect {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}
    
